* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgba(0, 0, 0, .9);
}

.container {
  margin-top: 70px;
}

.navbar {
  width: 100%;
  z-index: 1;
  background: #332d2d;
  top: 0;
  position: fixed !important;
}

.header {
  color: #fff;
  text-align: center;
  margin-left: 10px;
  font-size: 1.5em;
}

.header a {
  color: #fff;
  text-decoration: none;
}

h1 {
  text-align: center;
}

#website-title {
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  font-family: Tangerine, cursive;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: flex;
}

.list {
  opacity: 1;
  background: #332d2d;
  margin: 0 100px 0 0;
  padding: 0;
  list-style-type: none;
  -webkit-transition: opacity .2s ease-in-out;
  -moz-transition: opacity .2s ease-in-out;
  transition: opacity .2s ease-in-out;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: flex;
}

.display {
  -webkit-transition: opacity, height .2s ease-in-out;
  -moz-transition: opacity, height .2s ease-in-out;
  transition: opacity, height .2s ease-in-out;
  display: none;
  opacity: 0 !important;
}

.link {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  font-size: 1.1em;
  text-decoration: none;
  color: rgba(3, 180, 250, .56) !important;
}

.link:hover {
  background-color: rgba(128, 128, 128, .357);
  -webkit-transition: background-color, all .2s ease-in-out;
  -moz-transition: background-color, all .2s ease-in-out;
  transition: background-color, all .2s ease-in-out;
}

.nav-item {
  background-color: none;
  margin: 5px;
  text-decoration: none;
  -webkit-transition: background-color, all .2s ease-in-out;
  -moz-transition: background-color, all .2s ease-in-out;
  transition: background-color, all .2s ease-in-out;
}

.nav-item a {
  padding: 10px;
  display: inline-block;
}

.utilities {
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: flex;
  position: absolute;
  right: 12px;
}

.utilities a img {
  height: 35px;
  object-fit: cover;
  width: 35px;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  margin-left: 10px;
  -webkit-transition: -webkit-box-shadow .2s, box-shadow .2s;
  -moz-transition: -webkit-box-shadow .2s, box-shadow .2s;
  transition: -webkit-box-shadow .2s, box-shadow .2s;
  -webkit-box-shadow: 0 0 rgba(0, 0, 0, 0);
  box-shadow: 0 0 rgba(0, 0, 0, 0);
}

.utilities a img:hover {
  -webkit-transition: -webkit-box-shadow .2s, box-shadow .2s;
  -moz-transition: -webkit-box-shadow .2s, box-shadow .2s;
  transition: -webkit-box-shadow .2s, box-shadow .2s;
  -webkit-box-shadow: 0 0 15px #f0f8ff;
  box-shadow: 0 0 15px #f0f8ff;
}

.utilities #theme-icon {
  color: #fff;
  cursor: pointer;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0);
  padding: 0;
  font-size: 2em;
  -webkit-transition: text-shadow, all .2s ease-in-out;
  -moz-transition: text-shadow, all .2s ease-in-out;
  transition: text-shadow, all .2s ease-in-out;
  display: inline-block;
}

.utilities #theme-icon:hover {
  text-shadow: 0 0 20px #f0f8ff;
  -webkit-transition: text-shadow, all .2s ease-in-out;
  -moz-transition: text-shadow, all .2s ease-in-out;
  transition: text-shadow, all .2s ease-in-out;
}

#menu {
  cursor: pointer;
  text-shadow: 0 0 rgba(0, 0, 0, 0);
  -webkit-border-radius: 5px;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 2em;
  -webkit-transition: text-shadow, all .2s ease-in-out;
  -moz-transition: text-shadow, all .2s ease-in-out;
  transition: text-shadow, all .2s ease-in-out;
  display: none;
  position: absolute;
  top: 5px;
  right: 5px;
}

#menu:hover {
  text-shadow: 0 0 20px #f0f8ff;
  -webkit-transition: text-shadow, all .2s ease-in-out;
  -moz-transition: text-shadow, all .2s ease-in-out;
  transition: text-shadow, all .2s ease-in-out;
}

.animate {
  -webkit-animation-name: animate;
  -moz-animation-name: animate;
  animation-name: animate;
  -webkit-animation-duration: .3s;
  -moz-animation-duration: .3s;
  animation-duration: .3s;
}

@-webkit-keyframes animate {
  0% {
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    transform: scale(1.5);
  }

  100% {
    -webkit-transform: scale(1s);
    -moz-transform: scale(1s);
    transform: scale(1s);
  }
}

@-moz-keyframes animate {
  0% {
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    transform: scale(1.5);
  }

  100% {
    -webkit-transform: scale(1s);
    -moz-transform: scale(1s);
    transform: scale(1s);
  }
}

@keyframes animate {
  0% {
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    transform: scale(1.5);
  }

  100% {
    -webkit-transform: scale(1s);
    -moz-transform: scale(1s);
    transform: scale(1s);
  }
}

@media screen and (max-width: 600px) {
  #menu {
    display: inline-block;
  }

  .utilities {
    margin-top: 2.5px;
    position: absolute;
    top: -1px;
    right: 60px;
  }

  .navbar {
    height: 50px;
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-transition: height .2s ease-in-out;
    -moz-transition: height .2s ease-in-out;
    transition: height .2s ease-in-out;
  }

  .list {
    text-align: center;
    width: 100%;
    -webkit-box-orient: vertical;
    -moz-box-orient: vertical;
    -webkit-box-direction: normal;
    -moz-box-direction: normal;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    margin: 5px 0 0;
    padding-bottom: 5px;
  }

  .nav-item {
    width: 100%;
    margin: 0;
  }

  .nav-item a {
    width: 95%;
  }
}

.profile-container {
  position: relative;
  width: 150px !important;
}

.profile-container img {
  margin: 0 !important;
}

.profile-container label {
  color: #fff;
  cursor: pointer;
  background-color: gray;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  padding: 1.5px 5px;
  font-size: 1.1em;
  position: absolute;
  top: 100px;
  left: 95px;
}

.profile-container input {
  cursor: pointer;
  background-color: red;
}

h1 {
  text-align: right;
}

.info-container {
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: flex;
}

.info-section {
  color: #fff;
  width: 75%;
  background: -webkit-linear-gradient(45deg, rgba(0, 0, 0, .8), #f0f8ff);
  background: -moz-linear-gradient(45deg, rgba(0, 0, 0, .8), #f0f8ff);
  background: linear-gradient(45deg, rgba(0, 0, 0, .8), #f0f8ff);
  -webkit-border-radius: 10px;
  border-radius: 10px;
  padding: 10px;
}

